import React, { Component } from 'react';
import Link from 'gatsby-link';
import OfficialAccount from '../OfficialAccount';
import { hots } from '../../constants/hots';
import './style.css';

const Sidebar = ({ backgroundColor = '#F5F9FC' }) => (
  <div className="sidebar">
    <OfficialAccount backgroundColor={backgroundColor} />
    <div className="sidebar-hots" style={{ backgroundColor }}>
      <header></header>
      {hots.map(x => (
        <header key={x.node.fields.slug}>
          <Link to={x.node.fields.slug}>{x.node.frontmatter.title}</Link>
        </header>
      ))}
    </div>
  </div>
);

export default Sidebar;



